<template>
  <div>
    <v-app-bar color="dark" app text dark>
      <v-toolbar-title>
        <router-link to="/" tag="span" style="cursor: pointer"
          ><transition appear v-on:before-appear="customBeforeAppearHook">
            <img
              src="../../public/LogoSample_ByTailorBrands-_3_.svg"
              alt="logo"
            />
          </transition>
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-app-bar-nav-icon
        v-show="mobile"
        @click="togleDrawer"
      ></v-app-bar-nav-icon>

      <v-btn
        v-show="!mobile"
        color="dark"
        height="100%"
        elevation="0"
        small
        v-for="(item, i) in items"
        :key="i"
        @click="
          $vuetify.goTo(`#${item.name}`, options);
          setActive(i);
        "
        :name="item.name"
        :class="{
          active: activeIndex === i,
        }"
      >
        {{ item.title }}
      </v-btn>
    </v-app-bar>
    <v-navigation-drawer
      style="position: fixed; top: 50px; left: 0; overflow-y: scroll"
      v-model="drawer"
      absolute
      dark
      :overlay-opacity="0.5"
    >
      <v-list nav dense>
        <v-list-item-group>
          <v-list-item v-for="(item, index) in items" :key="index">
            <v-list-item-title
              @click="
                $vuetify.goTo(`#${item.name}`, options);
                setActive(index);
              "
              :name="item.name"
              :class="{
                active: activeIndex === index,
              }"
              >{{ item.title }}</v-list-item-title
            >
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      miniVariant: false,
      drawer: false,
      mobile: false,
      items: [
        {
          title: "Inicio",
          to: "/Inicio",
          name: "Inicio",
        },
        {
          title: "SobreMi",
          to: "SobreMi",
          name: "SobreMi",
        },
        { title: "Portafolio", to: "Portafolio", name: "Portafolio" },
        { title: "Habilidades", to: "habilidades", name: "Habilidades" },
      ],
      duration: 10,
      offset: 0,
      easing: "linear",
      activeIndex: 0,
    };
  },
  methods: {
    customBeforeAppearHook(el) {
      el.className = "animate__animated animate__zoomIn";
    },
    checkScreen() {
      this.windownWidth = window.innerWidth;
      if (this.windownWidth <= 550) {
        this.mobile = true;
        return;
      }
      this.mobile = false;
      return;
    },
    togleDrawer() {
      this.drawer = !this.drawer;
    },
    setActive(index) {
      this.activeIndex = index;
    },
  },
  created() {
    window.addEventListener("resize", this.checkScreen);
    this.checkScreen();
  },
  computed: {
    options() {
      return {
        duration: this.duration,
        offset: this.offset,
        easing: this.easing,
      };
    },
  },
};
</script>

<style>
.active {
  color: #ff5722 !important;
}
.v-btn--active.no-active::before {
  opacity: 0 !important;
}
</style>