<template>
  <div class="d-flex flex-column align-center justify-space-around">
    <h4>{{ item.title }}</h4>
    <div class="img-circulo mb-3">
      <v-img class="" :width="50" :src="item.logo"></v-img>
    </div>

    <v-progress-circular
      :rotate="item.rotate"
      :size="100"
      :width="10"
      :value="value"
      :color="item.color"
    >
      <strong>{{ value }}%</strong>
    </v-progress-circular>
  </div>
</template>

<script>
export default {
  props: ["item"],
  data() {
    return {
      value: 0,
    };
  },
  mounted() {
    this.interval = setInterval(() => {
      if (this.value === this.item.value) {
        return this.value;
      }
      this.value += 5;
    }, 1000);
  },
};
</script>

<style>
.img-circulo {
  height: 50px;
}
</style>