<template>
  <section class="text-center">
    <v-icon>{{ card.html }}</v-icon>
    <v-dialog
      :close-delay="1000"
      :fullscreen="true"
      v-model="dialog"
      width="100%"
      :scrollable="false"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-hover v-slot="{ hover }">
          <v-card
            class="mx-auto pointer"
            color="grey lighten-4"
            max-width="600"
          >
            <v-img
              :class="{ 'on-hover': hover }"
              height="250"
              style="width: 350px; max-width: 100%; padding: 0px"
              :src="card.img"
              alt=""
              v-on="on"
              v-bind="attrs"
            >
              <v-expand-transition>
                <div
                  v-if="hover"
                  class="
                    d-flex
                    flex-column
                    justify-center
                    align-center
                    transition-fast-in-fast-out
                    v-card--reveal
                    i-white
                    text-h2
                    white--text
                  "
                  style="height: 100%"
                >
                  <div class="d-flex flex-column justify-center align-center">
                    <h4>Hecho con:</h4>
                    <h4 class="d-flex">
                      <div v-for="(logo, index) in card.logo" :key="index">
                        <IconLogo :logo="logo" />
                      </div>
                    </h4>
                  </div>

                  <h4
                    class="pointer-preview d-flex justify-center align-center"
                  >
                    <i
                      @mouseover="hoverIn"
                      @mouseout="hoverOut"
                      :class="iconLogoAnimation"
                      class="fas fa-hand-pointer"
                    ></i>
                  </h4>
                </div>
              </v-expand-transition>
            </v-img>
          </v-card>
        </v-hover>
      </template>

      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Proyecto: {{ card.title }}
        </v-card-title>
        <v-card-text>
          <iframe
            class="iframe"
            v-if="dialog"
            width="100%"
            height="750"
            :src="card.url"
            title="YouTube video player"
            frameborder="0"
            allow="geolocation"
            allowfullscreen
          ></iframe>
        </v-card-text>
        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="deep-orange" text @click="dialog = false">
            Volver
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </section>
</template>

<script>
import IconLogo from "./IconLogo.vue";
export default {
  props: ["card"],
  components: {
    IconLogo,
  },
  data() {
    return {
      dialog: false,
      opacity: false,
      hover: false,
      iconDialog: false,
      active: false,
      animacion: "animate__animated animate__heartBeat",
    };
  },
  methods: {
    textLogo() {
      return (this.iconDialog = !this.iconDialog);
    },
    hoverIn() {
      this.active = true;
    },
    hoverOut() {
      this.active = false;
    },
  },
  computed: {
    iconLogoAnimation() {
      return this.active ? this.animacion : "";
    },
  },
};
</script>

<style >
.transparent {
  background-color: orange !important;
}

.v-card--reveal {
  background-color: black;
  opacity: 0.7;
}
.pointer {
  cursor: pointer;
}

.pointer-preview {
  background-color: orangered;
  border-radius: 100%;
  width: 100px;
  height: 100px;
}

i.v-icon.v-icon {
  color: white;
}

i.v-icon.v-icon:hover {
  color: orangered;
}
</style>